.about{
    margin-top: 145px;
}
.about__head {
    font-size: 30px;
}
.about__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1166px;
    text-align: center;
    padding: 0;
    margin: 44px auto auto auto;
}
.about__item {
    list-style: none;
    max-width: 263px;
}
.about__item strong {
    color: $green;
    font-weight: bold;
    font-size: 28px;
    line-height: 38px;
}
.about__item p {
    margin-top: 15px;
    font-size: 22px;
    line-height: 34px;
    letter-spacing: -0.5px;
}

@media (max-width:$tablet-width) {
    .about{
        margin-top: -100px;
    }
    .about__head {
        font-size: 23.8px;
        margin-top: 32px;
        margin-bottom: 0px;
    }
    .about__list {
        max-width:556px;
        margin-top: 35px;
    }
    .about__item {
        margin-bottom: 22px;
    }
}
@media (max-width:$mobile-width) {
    .about{
        margin-top: -30px;
    }
    .about__list {
        max-width:320px;
        margin-top: 33px;
    }
    .about__item:first-child p {
        margin-top: 16px;
    }
    .about__item {
        max-width: 266px;
        height: 125px;
        margin: auto auto 35px auto;
    }
    .about__item p {
        font-size: 18px;
        margin-top: 9px;
    }
}
